.App-logo { height: 40vmin; pointer-events: none; }

@media (prefers-reduced-motion: no-preference) {
    .App-logo { animation: App-logo-spin infinite 20s linear; }
}

.App-header { background-color: #282c34; min-height: 100vh; display: flex; flex-direction: column; align-items: center; justify-content: center; font-size: calc(10px + 2vmin); color: white; }

.App-link { color: #61dafb; }

@keyframes App-logo-spin {
    from { transform: rotate(0deg); }
    to { transform: rotate(360deg); }
}


/* custom styles */
* { box-sizing:border-box }
body { background-color:#f3f3f3 }
h1, h2, h3 { margin:0 0 10px }
a { color:inherit; text-decoration:none; }
#page-body {  background-color:white; max-width:1240px; margin:0 auto;padding:2em 2.6em; }
#page-body > h3 { border-top:1px solid #cecece; margin:2.2em -2.2em 0; padding: 1.2em 2.2em; }
#page-body a h3 { margin:0; }
#page-body a p { margin:0 0 10px; }

button { border:none; background-color:black; padding:10px; color:white; border-radius:4px; margin-right:14px; cursor:pointer; }

#upvotes-section button,
#upvotes-section p { display:inline-block; vertical-align:middle;  }

#comment-form { margin-top:30px; border:1px solid black; padding:20px 30px 30px; display:table;border-radius:4px; }
#comment-form label { display:block; font-weight:bold;}
#comment-form input, textarea { display:block; border:2px solid black; margin:5px 0 10px; padding:10px; border-radius:4px; width:350px; }
.comment h4 { margin:0; }
.comment p { margin:0 0 20px; }

nav { text-align:center; display:block; margin:1em 0; }
nav ul, nav li { margin:0; padding:0;list-style-type:none; }
nav li { display:inline-block; vertical-align:top; }
nav a { display:block; padding:10px 18px; margin:10px; background-color:white; }

